    /* QUESTION/TIMER/LEFT SECTION */
    .question-section {
    width: 100%;
    position: relative;
    margin-left: 20px;
    font-size:large;
    }
    
    .question-count {
    margin-bottom: 20px;
    }
    
    .question-count span {
    font-size: 28px;
    }
    
    .question-text {
    margin-bottom: 12px;
    }
    
    .timer-text {
    background: rgb(230, 153, 12);
    padding: 15px;
    margin-top: 20px;
    margin-right: 20px;
    border: 5px solid rgb(255, 189, 67);
    border-radius: 15px;
    text-align: center;
    }
    
    /* ANSWERS/RIGHT SECTION */
    .answer-section {
    width: 100%;
    margin-top: 130px;
    margin-right: 20px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    }
    
   
    
